.header {
  width: 100%;
  background-color: #d55845;
  position: fixed;
  top: 0;
  z-index: 1;
  .headerContent {
    width: 70%;
    margin: auto;
    outline: 0px solid red;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    color: white;
    .headerContentLeft {
      display: flex;
      align-items: center;
      .logo {
        font-size: 30px;
        margin-right: 20px;
      }
    }
    .headerContentRight {
      display: flex;
    }
  }
}
