.user {
    max-width: 60%;
    margin: 0 0 10px 30px;
    .userInfo {
        display: flex;
        .rightBlock {
            max-width: 100px;
            margin: 0 20px 0 0;
            outline: 0px solid red;
            img {
                width: 100%;
                border-radius: 50%;
                cursor: pointer;
            }
            .button {
                text-align: center;
                margin: 5px 0 0 0;
            }
        }
        .leftBlock {
            outline: 0px solid green;
            .status {
                font-weight: 500;
            }
            .userName {
                font-size: 20px;
                font-weight: 700;
                cursor: pointer;
                a {
                    color: black;
                }
            }
        }
    }
    .bottomLine {
        border-bottom: 2px solid orange;
        height: 5px;
    }
}
